@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: 4.2rem;

  padding: 0 5rem;

  background-color: white;
}

.Header__logo {
  height: 80%;
}

.Navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.Navbar__link {
  text-decoration: none;
  color: grey;

  font-weight: bold;
  font-size: 1.2rem;
  margin-right: 2.4rem;
}

.Navbar__link:hover {
  color: rgb(51, 182, 226);
}

.Hero {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;

  height: 100%;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  text-align: center;
}

.Hero__h1 {
  color: white;

  font-size: 3rem;
  margin-bottom: 2.2rem;
}

.Hero__h2 {
  color: white;
  font-size: 1.5rem;

  margin-bottom: 5rem;
}

.Hero__button {
  color: white;
  background-color: rgb(89, 212, 89);

  outline: none;
  border: none;
  border-radius: 3px;

  height: 3.3rem;
  width: 8rem;

  font-size: 1rem;

  cursor: pointer;
}

.Hero__button:hover {
  -webkit-filter: grayscale(40%);
          filter: grayscale(40%);
}

.Benefits {
  background-color: white;
  height: 300px;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.Benefits__card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.Benefits__image {
  height: 100px;
}

.Benefits__text {
  font-size: 1.5rem;
  color: rgba(0, 0, 0, 0.8);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

body {
  font-family: 'Nunito', sans-serif;
}

.Benefits__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 50px;
  color: rgba(0, 0, 0, 0.8);

  text-align: center;
}

.Footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  height: 100px;
}

